import React from "react";
import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";
import { Container } from "./SliderOwlElements.js"
import author1 from "../../../../img/gabriel-lindo.jpg"
import author2 from "../../../../img/daniel-sartori.jpeg"
import author3 from "../../../../img/mario-perino.jpeg"


const options = {
    margin: 10,
    responsiveClass: true,
    nav: true,
    dots: true,
    autoplay: false,
    smartSpeed: 1000,
    responsive: {
        0: {
            items: 1,
        }
    },
};

export const OwlSlider = () => {
    return (
        <Container>
            <OwlCarousel style={{ marginTop: "80px" }} className="owl-theme" loop {...options}>
                <div className="item">
                    <div className="inner-item">
                        <p>Despite a three-day deadline, Foton Technologies outpaced the schedule and delivered an exceptional frontend integration. They communicated consistently, updating the internal team on the project status and helping to manage client expectations. Their support helped ensure a positive experience.</p>
                    </div>

                    <div className="info-authour">

                        <div className="info-authour-img">
                            <img src={author2} alt="authour" />
                        </div>

                        <div className="info-authour-txt">
                            <h5>Daniel Santori</h5>
                            <h6>Head of Sales at Bossabox</h6>
                        </div>

                    </div>

                </div>

                <div className="item">

                    <div className="inner-item">
                        <p>For me Foton was born with a great team spirit for delivering fast, concise and great quality results.</p>
                    </div>

                    <div className="info-authour">

                        <div className="info-authour-img">
                            <img src={author1} alt="authour" />
                        </div>

                        <div className="info-authour-txt">
                            <h5>Gabriel Rubens</h5>
                            <h6>Gabriel Rubens</h6>
                        </div>

                    </div>

                </div>

                <div className="item">


                    <div className="inner-item">
                        <p>Foton Technologies made expert tech decisions to build an exceptional and high-quality product. They met expectations. Their effective planning supplemented their professional management style. Customers can expect a communicative and hardworking team.</p>
                    </div>

                    <div className="info-authour">

                        <div className="info-authour-img">
                            <img src={author3} alt="authour" />
                        </div>

                        <div className="info-authour-txt">
                            <h5>Mario Perino</h5>
                            <h6>CFO at Provi</h6>
                        </div>

                    </div>
                </div>

            </OwlCarousel>
        </Container >
    )
}