import styled from "styled-components"


export const Container = styled.div`
    width: 100%;
    margin-top: 80px;

    p{
        margin-bottom: 40px;
        color: #fff;
        font-size: clamp(1.5rem, 2.3vw, 3rem);
    }

    .owl-theme{
        h2, p{
            width: 70%;
            margin: 0 auto;
        }
    }

    .info-authour{
        width: 100%;
        margin: 0 auto;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 30px;


        .info-authour-img{
            width: 100px;

            img{
                width: 100%;
                border-radius: 100%;
            }
        }


        .info-authour-txt{
            width: 60%;
            height: 100%;
            color: #fff;
            margin-left: 20px;

            justify-content: center;
            align-items: center;
            margin-top: 30px;

            h5{
                font-family: ${props=> props.theme.font_titulo};
                margin-top: -30px; 
                font-size: clamp(1rem,2.5vw, 1.8rem)
            }   

            h6{
                font-size: clamp(1rem,2.5vw, 1.3rem)
            }
        
        }
    }
`