import styled from "styled-components"


export const Container = styled.div`
    width: 100%;
    height: 80vh;
    background: ${props => props.theme.primary_color};
    position: relative;
    top: 10px;
`

export const Main = styled.div`
    width: 100%;
    height: 100vh;
    background: #181830;
    display: flex;


@media (max-width: 750px){
    flex-direction: column;
    height: fit-content;
    padding-bottom: 30px;
}


.coluna-main{
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;


    @media (max-width: 750px){
        width: 100%;
    }

    h1{
        color: #fff;
        font-size: clamp(1.9rem, 3.7vw, 5rem);
        font-family: ${props=> props.theme.font_titulo};
        margin-left: 70px;

        @media (max-width: 750px){
            width: 90%;
            text-align: center;
            margin: 50px auto;
        }

    }

    img{
        width: 60%;

        @media (max-width: 750px){
            width: 80%;
        }
    }
}
`
