import styled from "styled-components"
import bgBanner from "../../../img/bgBanner.png"

export const BannerHome = styled.div`
    width: 100%;
    height: 80vh;
    background-image: url(${bgBanner});
    background-size: cover;

    @media (max-width: 750px){
        height: fit-content;
    }

    display: flex;
    justify-content: center;
    flex-direction: column;

    @media (min-width: 950px){
        flex-direction: row;
    }

    .txtBanner{
        width: 100%;
        height: 100%;
        margin: 0 auto;
        padding-left: 30px;

        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;


        h1{
            font-size: clamp(2rem, 3.8vw, 4.5rem);
            font-family: ${props=> props.theme.font_titulo};
            text-align: left;
            width: 90%;
            color: #fff;
            text-align: left;
            margin: 30px auto;
            margin-top: 60px;

            @media (min-width: 750px){
                margin-top: 0px;
            }
        

        }
        h6{
            font-size: clamp(1rem, 3vw, 1.4rem);
            width: 90%;
            color: #fff;
            text-align: left;
            margin: 0 auto;
            margin-bottom: 60px;
            padding-top: 40px;
            font-weight: lighter;

        }
    }

        
    .txtBannerMarcas{
        width: 100%;
        height: 100%;
        margin: 0 auto;

        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row;

        .wrap-grid{
            width: 80%;
            margin: 0 auto;

            @media (max-width: 750){
                width: 100%;
            }

            .box-icone{
                width: 100%;
                text-align: left;
                margin: 30px 0;


                @media (max-width: 750px){
                    display: flex;
                    justify-content: center;
                
                }
                
                img{
                    width:clamp(20px, 2.5vw, 40px);
                    height: clamp(20px, 2.5vw, 40px);
                    margin-right: 15px;
                    float: left;
                }

                p{
                    color: #fff;
                    font-size: clamp(1rem, 2.5vw, 1.5rem);
                    font-family: ${props=> props.theme.font_titulo};
                
                }
            }


        }


    }

    `