import styled from "styled-components"

export const Container = styled.div`
    width: 100%;
    height: fit-content;
    background: #000;
    padding: 20px;

    @media (min-width: 1100px){
        height: 100vh;
    }

    h1{
        width: 90%;
        color: #fff;
        font-size: clamp(2rem, 3.7vw, 5rem);
        font-family: ${props=> props.theme.font_titulo};
        margin-left: 30px;
        margin-top: 30px;

        @media (min-width: 950px){
            margin-left: 200px;
        }
    }
    
`