import React from 'react'
import { Container, Main} from "./HomeElements"
import { Banner } from "./BannerHome/BannerHome"
import {Avaliation} from "./Avaliation/Index"
import {Footer} from "../Footer/Index"
import imgMain1 from "../../img/main1.png"
import imgMain2 from "../../img/main2.png"
import imgMain3 from "../../img/main3.png"



export const Home = () => {
    return (
        <Container>
            <Banner />

            <Main>
                <div class="coluna-main">
                    <h1>Sense connects the world through Blockchain</h1>
                </div>

                <div class="coluna-main">
                    <img src={imgMain1} alt="main content" />
                </div>
            </Main>


            <Main style={{ background: "#385373" }}>
                <div class="coluna-main" >
                    <img src={imgMain2} alt="main content" />
                </div>
                <div class="coluna-main" style={{ marginRight: "70px" }}>
                    <h1>Squirrel is your favorite blockchain portfolio manager</h1>
                </div>
            </Main>


            <Main style={{ background: "#121F41" }}>
                <div class="coluna-main">
                    <h1>AngoraLabs measure and improve platforms experience</h1>
                </div>

                <div class="coluna-main">
                    <img src={imgMain3} alt="main content" />
                </div>
            </Main>

            <Avaliation/>
            <Footer/>
            

            

        </Container>
    )
}
