import styled from "styled-components"

export const Container = styled.div`
    width: 100%;
    height: 40vh;
    background: #fff;
    padding: 0 30px;


    display: flex;
    justify-content: center;
    flex-direction: column;

    @media (min-width: 1100px){
        padding: 0 90px;
    }

    h6{
        font-size: clamp(2rem, 2.5vw, 2rem);
        margin-bottom: 20px;
    }

    h1{
        font-weight: bold;
        font-size: clamp(2rem, 4vw, 3.5rem);
        color: #000;
    }
`